import React from "react";

import Menu from "./src/layout/menu";
import Scripts from "./src/scripts";

// Wraps every page in a component
export const wrapPageElementShared = (props) => {
    let menuRoute = props.props.data?.page?.menu ? props.props.data.page.menu : '';
    return (
        <>
            <Menu route={menuRoute} curRoute={props.props.location.pathname} />
            {props.element}
            <Scripts />
        </>
    );
};